
import RouteMatcher from '@/utils/route-matcher'
import Html5History from '@/history/Html5History'


export default class Router {
  constructor (mount) {
    this._mount = document.getElementById(mount)
    this._subRouteView = '<div id="sub-route-view"></div>'
    this._subMount = null
    this._isPassing = false

    this._cache = {}
    this._middlewares = []

    this.routeIdx = 0

    if (!this._mount) {
      throw new Error('Can not get mount point...')
    }

    this._matcher = new RouteMatcher()

    this.iterator = false

    this._history = new Html5History({
      matcher: this._matcher
    })
  }

  /**
   * 
   * 
   * @memberof Router
   */
  render (dom) {
    console.log(`is sub ? ${this._isPassing}`)
    if (this._isPassing) {
      this._subMount.innerHTML = dom
    } else {
      this._mount.innerHTML = dom
    }
  }

  next (dom) {
    this._mount.innerHTML = dom
    this._isPassing = this._history.getMatchedCount() > 1 ? true : false
    this._subMount = document.querySelector('#sub-route-view')
  }

  subRoute() {
    return this._subRouteView
  }

  use (middleware) {
    this._middlewares.push(middleware)
  }


  /**
   * 
   * 
   * @param {any} path 
   * @param {any} middleware 
   * @memberof Router
   */
  route (path, middleware) {
    const self = this
    this._matcher.add(path, (request) => {
      if (path !== '*') {
        for (let i=0; i<this._middlewares.length; i++) {
          this._middlewares[i](request, this, this.next.bind(this))
        }
      }
      middleware(request, this, this.next.bind(this))
    })
  }

  /**
   * 
   * 
   * @param {string} url 
   * @param {Object} body 
   * @memberof Router
   */
  go (url, body) {
    this._isPassing = false
    this._history.go(url, body)
  }

  /**
   * 
   * 
   * @memberof Router
   */
  redirect (url, body) {
    this._isPassing = false
    this._history.redirect(url, body)
  }

  /**
   * 
   * 
   * @memberof Router
   */
  back () {
    this._isPassing = false
    this._history.back()
  }

}



