
const session = sessionStorage
const prefix = 'smer'
export function setCache (id, body) {
  session.setItem(`${prefix}${id}`, JSON.stringify(body))
}

export function getCache (id) {
  try {
    const cache = session.getItem(`${prefix}${id}`)
    return cache 
      ? JSON.parse(cache)
      : null;
  } catch(err) {
    throw 'parse body err'
  }
}



