import { getType } from '@/utils'
import { getCache, setCache } from '@/utils/cache-body'

export default class Html5History {
  /**
   * Creates an instance of Html5History.
   * @param {Object} options 
   *             
   * @memberof Html5History
   */
  constructor (options) {
    this.matcher = options.matcher
    this._matchedCount = 0

    window.addEventListener('load', this._listen.bind(this))
    window.addEventListener('popstate', this._listen.bind(this))
  }

  _listen (event) {
    const path = `${location.pathname}${location.hash}${location.search}`
    const matchedRoutes = this.matcher.match(path)
    this._matchedCount = matchedRoutes.length

    this._fireHandlers(matchedRoutes, event.state)    
  }

  _routeTo (url, body) {
    const matchedRoutes = this.matcher.match(url)
    this._matchedCount = matchedRoutes.length

    this._fireHandlers(matchedRoutes, body)
  }

  _fireHandlers(matchedRoutes, body) {
    for (let i=0; i<matchedRoutes.length; i++) {
      const item = matchedRoutes[i]
      const cache = this._getCache(item)

      item.handler({
        body: body || cache,
        query: item.query,
        params: item.params
      })

      this._cacheBody(body, item)
    }
  }

  _getCache(routeConfig) {
    return getCache(routeConfig._id)
  }

  _cacheBody(state, routeConfig) {
    if (state) {
      setCache(routeConfig._id, state)
    }
  }

  go (url, body) {
    history.pushState(body, '', url)
    this._routeTo(url, body)
  }

  redirect (url, body) {
    history.replaceState(body, '', url)
    this._routeTo(url, body)
  }

  back () {
    history.go(-1)
  }

  getMatchedCount() {
    return this._matchedCount
  }



}



