import './main.scss'
import Router from '../src/index'
import spring from './pages/spring'
import { summer, morn, nightfall, foo } from './pages/summer'
import autumn from './pages/autumn'
import winter from './pages/winter'

const app = document.getElementById('app')
const router = new Router('router-view')

// route config
router.route('/spring/:year', spring)
router.route('/summer/:year', summer)
router.route('/summer/:year/morn', morn)
router.route('/summer/:year/nightfall', nightfall)
router.route('/autumn/:year', autumn)
router.route('/winter/:year', winter)

// redirect example
router.route('*', (req, res, next) => {
  res.redirect('/spring/1945?month=10&day=24')
})

// middleware example
router.use((req) => {
  if (!req.body) req.body = {}
  req.body.parsedByMiddleware = `${req.params.year}-${req.query.month}-${req.query.day}`
})

// middleware example 2
router.use((req) => {
  if (req.body && req.body.mes) {
    req.body.mes += ' ~~'
  }
})

router.route('/index', (req, res, next) => {
  const { query, params, body } = req

  next(`
    <div>Welcome to index page</div>
    <strong>I'am a parent route-view</strong>
    ${res.subRoute()}
  `)
})

router.route('/index/test', (req, res, next) => {
  const { query, params, body } = req

  res.render(`
    <div style='color: red'> hei I'am a sub route-view</div>
    ${query ? `<div> count: ${query.count} </div>` : ''}
  `)
})

router.route('/user/:id', (req, res, next) => {
  const { query, params, body } = req

  res.render(`
    <div>This is user page</div>
    <div> my name is ${params.id} </div>
    ${body && body.mes ? `<div> hei mes is ${body.mes} </div>` : 'nothing'}
  `)
})


